@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");

.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 16px;
  gap: 16px;
}

.NavBar {
  width: 100%;
  background-color: #fef3ea;
}

.NavBar__Logo {
  max-width: calc(100% - 32px);
  margin: 16px;
}

.SelectionCard {
  height: 210px;
  border-radius: 25px;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.SelectionCard__TitleSection {
  padding: 8px;
  background: rgba(0, 0, 0, 0.5);
  height: 50px;
  border-radius: 0 0 25px 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.SelectionCard__TitleSection span {
  font-size: 2em;
  padding-left: 8px;
  display: inline-flex;
  align-items: center;
  color: white;
}

.SelectionCard__TitleSection img {
  font-size: medium;
  width: 50px;
  color: white;
  fill: white;
}
